<script>
  // import { onMount } from "svelte";
  import { fade, fly, slide } from "svelte/transition";
  import { Router, Link, Route } from "svelte-routing";
  import { db } from "./firebase";
  import { collectionData } from "rxfire/firestore";
  import { startWith } from "rxjs/operators";

  let servselecionado = "";
  let nomesite = "";
  let urlsite = "";
  let servernome = "";
  let nrancher = "";
  let npma = "";
  let nip = "";
  let desativado = false;

  const addServer = e => {
    console.log(e);
    // itemsTab1 = [...itemsTab1, novoItem];
    db.collection("servidores").add({
      nome: servernome,
      ip: nip,
      rancher: nrancher,
	  pma: npma,
	  created: Date.now()
	});
	servernome = "";
	nip = "";
	nrancher = "";
	npma = "";
  };

  const addSite = e => {
    console.log(e);
    // itemsTab1 = [...itemsTab1, novoItem];
    db.collection("sites").add({
      nome: nomesite,
      servidor: servselecionado,
      url: urlsite,
      desativado: desativado,
      created: Date.now()
    });
    nomesite = "";
	urlsite = "";
	desativado = false;
  };

  function removeItem(event) {
    event.preventDefault();
    // console.log(event.target.name);
    // console.log(event.detail);
    const id = event.target.name;
    // const { id } = event.detail;
    console.log(id);
	var r = confirm("Tem certeza que deseja deletar esse site?");
    if (r == true) {
      db.collection("sites")
        .doc(id)
        .delete();
    } else {
      return false;
    }
  }

  function removeServer(event) {
    event.preventDefault();
    // console.log(event.target.name);
    // console.log(event.detail);
    const id = event.target.name;
    // const { id } = event.detail;
    console.log(id);

    var r = confirm("Tem certeza que deseja deletar o servidor?");
    if (r == true) {
      db.collection("servidores")
        .doc(id)
        .delete();
    } else {
      return false;
    }
  }

  function selecionarCopiar(event) {
    const input = document.getElementById(event.target.id);
    console.log(input);
    input.focus();
    input.select();
    document.execCommand("copy");
  }

  export let uid;

  const queryserv = db.collection("servidores").orderBy("nome");
  const servidores = collectionData(queryserv, "id").pipe(startWith([]));

  const querysite = db.collection("sites").orderBy("nome");
  const sites3lados = collectionData(querysite, "id").pipe(startWith([]));
  // console.log(servidores);

  let mostrar = false;
  const toggleMostrarForms = () => (mostrar = !mostrar);
</script>

<style>
  .forms {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }
  .formsites {
    margin: 20px;
    display: flex;
    flex-direction: column;
  }
  .formsites > label {
    /* flex: 1; */
    display: block;
    margin: 10px 10px;
  }
  .listasites a:first-child {
    flex: 35;
  }
  .listasites a:last-child {
    flex: 1;
    color: #e26d5a;
  }
  .listasites a:last-child:hover {
    background-color: #ffc85720;
  }
  .listasites {
    display: flex;
  }
  .sites {
	display: grid;
	grid-column-gap: 15px;
	grid-row-gap: 15px;
    grid-template-columns: repeat(auto-fit, minmax(450px, 1fr));
  }
  .servers {
    padding: 30px;
    border-bottom: 1px solid #eeeeee20;
    /* margin: 15px; */
    background-color: #00000020;
    border-radius: 9px;
    transition: all 400ms ease;
  }
  .detalhes-server h6 {
    font-size: 15px;
    line-height: 1.3em;
    margin: 0;
    color: #ffffff60;
    font-family: Fira Sans;
    text-align: center;
  }
  .detalhes-server h6:first-child {
    font-size: 17px;
    letter-spacing: 1px;
    color: #ffffff90;
  }
  .detalhes-server h6:hover {
    color: #fff;
  }
  .uk-accordion-title {
    font-size: 1.4em;
    font-family: "Montserrat Subrayada", sans-serif;
  }
  li.uk-open > a {
    color: #ffde99 !important;
  }
  h6 input.ip {
    background-color: transparent;
    border: 0;
    padding: 10px;
    color: #fff;
    text-align: center;
    font-size: 1.1em;
    font-family: "Fira sans";
    letter-spacing: 1px;
    font-style: italic;
  }
  a.inativo{
	  text-decoration: line-through;
	  background-color:#e26c5aaf;
  }
  .btx{
	  text-align: center;
  }
</style>

<div class="main">
  <div class="sidebar esquerda" />

  <div class="centro">

    <div class="colunas nested">
      <h1>
        Sites
        <button
          uk-icon="icon: {mostrar ? 'minus-circle' : 'plus-circle'}"
          class="uk-button uk-button-body"
          on:click={toggleMostrarForms}
          style="color:#fff" />
      </h1>

      {#if mostrar}
        <div
          class="uk-card uk-card-primary uk-width-1-1@m forms"
          style="padding:30px;"
          transition:slide>
          <div class="formsites">
            <h2>Adicionar Site</h2>
            <label>
              Site
              <input
                class="uk-input"
                type="text"
                placeholder="Nome"
                bind:value={nomesite} />
            </label>
            <label>
              Link
              <input
                class="uk-input"
                type="text"
                placeholder="URL"
                bind:value={urlsite} />
            </label>
            <label>
              Desativado?
              <input
                type="checkbox"
                class="uk-checkbox"
                bind:checked={desativado} />
            </label>

            <label>
              Servidor
              <select class="uk-select" bind:value={servselecionado}>
                <option value="" />
                {#each $servidores as server}
                  <option value={server.nome}>{server.nome}</option>
                {/each}
              </select>
            </label>
            <button
              on:click={addSite}
              class="uk-button uk-button-default"
              style="margin: 10px">
              Adicionar Site
            </button>
          </div>

          <div class="formsites">
            <h2>Adicionar Servidor</h2>
            <label>
              Servidor
              <input
                required
                class="uk-input"
                type="text"
                placeholder="Nome"
                bind:value={servernome} />
            </label>
            <label>
              IP
              <input
                required
                class="uk-input"
                type="text"
                placeholder="ip"
                bind:value={nip} />
            </label>
            <label>
              Rancher
              <input
                required
                class="uk-input"
                type="text"
                placeholder="URL"
                bind:value={nrancher} />
            </label>
            <label>
              PMA
              <input
                class="uk-input"
                type="text"
                placeholder="URL"
                bind:value={npma} />
            </label>
            <button
              on:click={addServer}
              class="uk-button uk-button-default"
              style="margin: 10px">
              Adicionar Servidor
            </button>
          </div>
        </div>
      {/if}

      <!-- id = itemsTab1.filter(t => t.selected)[0].id; -->
      <div class="sites">
        {#each $servidores as server}
          <div class="servers">

            <ul uk-accordion="collapsible: true">
              <li>
                <a class="uk-accordion-title" href="#">{server.nome}</a>
                <div class="uk-accordion-content">
                  <div class="detalhes-server">
                    <h6>
                      <input
                        value={server.ip}
                        on:click={selecionarCopiar}
                        id={server.ip}
                        class="ip" />
                    </h6>
					{#if server.rancher == "easyengine"}
						<h6>
							<a href="#ee">Easyengine</a>
						</h6>
					{:else}
						<h6>
							<a target="_blank" href={server.rancher}>Rancher</a>
						</h6>
						<h6>
							<a target="_blank" href={server.pma}>PMA</a>
						</h6>
					{/if}
                    <h6>
                      <a
                        href="#remove"
                        name={server.id}
                        on:click={removeServer}
                        style="color:red">
                        deletar
                      </a>
                    </h6>
                  </div>
                </div>
              </li>
            </ul>

            {#each $sites3lados as site, i}
              {#if site.servidor == server.nome}
                {#if site.desativado}
                  <div class="listasites" transition:slide>
                    <a href="//{site.url}" class="inativo">{site.nome}</a>
                    <a href="#remove" class="btx" name={site.id} on:click={removeItem}>x</a>
                  </div>
                {:else}
                  <div class="listasites" transition:slide>
                    <a href="//{site.url}">{site.nome}</a>
                    <a href="#remove" class="btx" name={site.id} on:click={removeItem}>x</a>
                  </div>
                {/if}
              {/if}
            {/each}
          </div>
        {/each}
      </div>
    </div>
  </div>
</div>
